		"Objectifs du projet": "Réouverture de la route entre Kaga-Bandoro et Ndélé. Le projet Le projet comprend trois types d’activités :   - Amélioration du drainage par réhabilitation de petits ouvrages d’art et du pont sur la Bamingui et entretien des bas cotés en utilisant la méthode des Travaux à Haute Intensité de Main d’Œuvre (THIMO);   - Protection et entretien des routes par la mise en place de barrières de pluie et la formation des équipes de cantonnage, et par des travaux mécanisés de reprofilage et de compactage par la MINUSCA;   - Renforcement de capacités des institutions par le redéploiement des Chefs de Service Préfectoraux du MINTP à Kaga-Bandoro et Ndélé, leur formation et la prise en charge des frais, et la réhabilitation de leurs bureaux à Kaga-Bandoro et Ndélé.",
