		"Nom du projet": "Programme d'Appui au Mécanisme de Coordination du PIR 10ème FED (PAMEC)",
		"Objectifs du projet": "L’objectif global du programme est de contribuer à une meilleure gouvernance et suivi des ressources et projets régionaux par les deux OR et à l’approfondissement de la coordination et de la convergence des projets d’intégration régionale en Afrique centrale.L’objectif global du programme est de contribuer à une meilleure gouvernance et suivi des ressources et projets régionaux par les deux OR et à l’approfondissement de la coordination et de la convergence des projets d’intégration régionale en Afrique centrale.      L’objectif spécifique du programme est d’accroître les capacités institutionnelles de la CEMAC et de la CEEAC en matière d’appropriation, de capacités d’intervention, d’échange d’informations et de coordination dans le cadre de la mise en œuvre du PIR 10ème FED, ainsi que de faciliter l'harmonisation sectorielle et la rationalisation entre les deux CER.",
