		"Objectifs du projet": "l'objectif général de cette action est d'aider le gouvernement Centrafricain à promouvoir une croissance durable et inclusive et à consolider et ameliorer la gouvernance démocratique et économique, tout en contribuant à réduiire la pauvrété. Les objectifs spécifiques sont de soutenir la feuille de route, et le plan d'urgence pour le rélevement durable du Gouvernement et de Transition en l'aidant à: favoriser un processus de transition vers le développement et la gouvernance démocratique; accroître la capacité financière du Gouvernement pour restaurer la paix, rétablir la stabilité macroécomique et atteindre les objectifs de politiques à court terle; soutenir les efforts du gouvernement pour garantir les fonctions vitales de l'Etat (en particulier la réalisation des fonctions administratives esentielles et la garantie d'un minimum de services de base aux populations.",
