		"Nom du projet": "Programme d'appui à la consolidation de l'Etat Centrafricain (SBC 2017-2019)",
		"Objectifs du projet": "l'objectif global du SBC 2017-2019 est d'appuyer les efforts des autorités nationales en faveur du relèvement et de la consolidation de la pais selon les trois axes retenus, à savoir le soutien à la paix, la sécurité et la réconciliation, le renouvellement du contrat social entre l'Etat et la population et le relèvement économique et la relance des secteurs productifs. les objectifs spécifiques sont les suivants: soutenir les fonctions vitales de l'Etat, sa capacité à reconstruire et developper ses structures, à consolider la paix et la réconcilitation, à contribuer à la relance du développement économique et social pour tous; participer au financement conjoint du déficit budgetaire et notamment assurer le paiement des rémunérations des fonctionnaires, la réalisation des fonctions administratives essentielles et la garantie des services de base maximaux aux populations.",
