		"Objectifs du projet": "Le projet vise à Rétablir en partie les services d'éducation et contribuer à la relance du système éducatif en protégeant les enfants affectés par le conflit Pour cela deux OS: 1,- Appuyer la reprise et le renforcement des services d’éducation dans les zones accessibles du pays, avec une attention particulière aux régions affectées par le conflit de l’Ombella Mpoko, Kémo, Nana Gribizi, Bamingui-Bangoran et Bangui.   2,- Contribuer à la restauration progressive et durable du système éducatif. Le projet se focalise sur 4 axes stratégiques :      Axe-1 : Accès à l’éducation   Axe-2 : Qualité de l’éducation   Axe-3 : Gestion, Gouvernance, Suivi et Évaluation   Axe-4 : Protection de l’enfance",
