		"Objectifs du projet": "L’objectif principal de l’action est de contribuer à un meilleur accès aux services de santé à travers un appui institutionnel qui pourrait consolider les acquis des réponses aux urgences tout en renforçant le système de santé. Pour ce faire la présente action permettra de renforcer particulièrement l’opérationnalité de la pyramide sanitaire et le leadership du Ministère de la santé à travers les volets suivants :   • Contribuer au renforcement du leadership du ministère de la santé dans la gouvernance du système de santé   • Appuyer le fonctionnement des directions techniques, des régions sanitaires et des districts/préfectures sanitaires    • Renforcer les capacités des structures sanitaires dans les domaines prioritaires (Système d’information sanitaires, surveillance épidémiologique et prescription médicale)",
