		"OBSERVATION": "Résultats attendus :   1. Renforcement des capacités transversales de Gestion de Cycle de Projet (GCP) de membres d’ONG locales ;   2. renforcement des capacités locales de travailleurs de la santé ayant reçu une formation spécifique sur le traitement des urgences médicales pédiatriques et gynéco-obstétricales;   3. amélioration de l’accès aux structures sanitaires pour les groupes vulnérables de femmes et d’enfants afin d’accroître la couverture sanitaire et la qualité de services délivrés au niveau national : un système de référence sauve-vie mis en place entre 3 dispensaires situés dans les districts sanitaires de Bimbo et Begoua (Sôh, Nzilla, Gbazéré) et Bangui ;    4. augmentation de l’offre de soins sauve-vie qualifiés et accessibles pour les enfants et les femmes dans un rayon de 30km de Bangui, dans des zones où l’accès à ces services est empêché à l’heure actuelle.",
