		"Nom du projet": "Facilité technique de Coopération (FCT IV)",
		"Objectifs du projet": "L'objectif global de cette Facilité est de poursuivre l’appui apporté à l'exécution de la stratégie de développement du gouvernement par le biais de programmes de développement pertinents financés par le Fonds européen de développement (FED), ainsi qu'à une meilleure maîtrise des questions de développement par les acteurs en RCA.   L'objectif spécifique est un appui à la mise en œuvre de la coopération RCA/UE grâce notamment à la réalisation d'études d'identification et de faisabilité, à des missions de suivi et de monitoring des programmes, à la sensibilisation et la formation des acteurs clés de la coopération en RCA et à la prise en charge de séminaires et conférences. C'est ainsi que le programme permettra en particulier l'identification et la formulation des interventions du PIN 11°FED (2014-2020)   les principales activités réalisées sont: (1) Appui au renforcemen des cadres nationaux aux travaux préparatoitres des documents de projets de présentation à la table ronde des bailleurs de fonds de Bruxelles, novembre 2016. (2) financement des déplacements de la délégation centrafricaine à la conférence des bailleurs de fonds tenue à Bruxelles en novembre 2016. appui aux processus électorales 2015(ANE). Financement complementaire au basket fond(PACEC). fiancement des missions et séminaires de formation au profits des cadres nationaux et des oraganisations de la société civile dans son ensemble.",
