		"Objectifs du projet": "Ce programme a pour objectif de recapitaliser et diversifier les moyens de production de groupes agropastoraux et de femmes et pérenniser leurs accès aux infrastructures économiques susceptibles de redynamiser les filières porteuses. Il s'agit plus spécifiquement d'accompagner les paysans dans la saison agricole de 2015 et de développer les bases pour celle de 2016 dans des zones qui sont historiquement considérées « le grenier de la RCA » en fournissant des intrants (semences, outils) ainsi que de développer un appui aux filières de base",
