		"Objectifs du projet": "Ce projet continue une action existante, en coordination avec la mission EUTM de conseil militaire de l'UE en RCA, ayant permis d’améliorer significativement les conditions de travail, de renforcer les capacités de gestion des ressources humaines des FACA et de sécurisation physique et informatique. Le projet vise à consolider les avancées en contribuant aussi à la réintégration des ex militaires et combattants dans la vie civile grâce à l'établissement d'un centre polyvalent de formation",
